import { Injectable } from '@angular/core';
import { Recipe } from "./recipe";
import { Ingredient } from "./ingredient";

@Injectable()
export class RecipeService {

    recipes: Recipe[];
    categories: string[];

    constructor() {
        this.init();
    }

    init() {
        //just mocked data for now
        this.categories = ["Vegan","Fish","Meat"];

        let ing1 = new Ingredient("ketchup","1l");
        let ing2 = new Ingredient("nudln","100g");
        this.recipes.push(new Recipe(0,"Spagetti","spagettiiiis", "nudln mit Ketchup",[ing1,ing2],["Vegan"]));
        this.recipes.push(new Recipe(0,"Steak","Beef Stake", "well done beef stake",[ing1,ing2],["Meat"]));
        this.recipes.push(new Recipe(0,"Spagetti Bolognese","spagettiiiis mit Fleisch", "nudln mit Ketchup und Fleisch",[ing1,ing2],["Meat"]));
        this.recipes.push(new Recipe(0,"Salat","green Salat", "Salat Gurk und Tomaten vorbereiten und Dressing dazu",[ing1,ing2],["Vegan"]));
        this.recipes.push(new Recipe(0,"Spagetti","spagettiiiis", "nudln mit Ketchup",[ing1,ing2],["Fish"]));
    }

    getRecipies(): Recipe[] {
        return this.recipes;
    }

    addRecipe(id:number, name:string, description:string, preparation:string, ingredients:Ingredient[],categories:string[]) {
        this.recipes.push(new Recipe(id,name,description,preparation,ingredients,categories))
    }

    getCategories() {
        return this.categories;
    }

}
